.Footer{
    padding: 4rem 6 rem;
    background-color: black;
    color: white;
}
.top{
    display: flex;
    align-items: center;
    text-align: start;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-right: 2rem;
}
.top i{
    color:#fff;
    font-size:2rem;
    margin-left: 2rem;
}
.top i:hover{
    color:#01959a;
}
.bottom{
    padding-top:2rem;
    text-align: start;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    
}
.bottom div{
    display: flex;
    flex-direction: column;
}
.bottom h4{
    font-size: 1.3rem;
    padding:1rem 0 0.8rem 0;
}
.bottom a{
    text-decoration: none;
    color: #fff;
    padding-bottom: 0.4rem;
    font-size: 1.1rem;
}
@media screen and (max-width:850px){
    .Footer{
        padding: 4rem 2rem;

    }
    .Footer i{
        margin:1rem 1rem 0 0;
    }
    .bottom div{
        width: 50%;
    }

}