.hero{
  width: 100%;
  height: 80vh;
  position: relative;
}
.hero-mid{
  width: 100%;
  height: 70vh;
  position: relative;
}
img{
  width:100%;
  height: 100%;
  object-fit: cover;
}
.hero-text{
  position: absolute;
  top: 50%;
  left: 65%;
  transform: translate(-50%,-50%);
  width:100%;
}
.hero-text h1{
  font-size: 5rem;
  font-weight: 800;
  background:#fff;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.hero-text p{
  font-size: 1.3rem;
  color:#fff;
  padding:0.5rem 0 2rem 0;
}
.hero-text .show{
  text-decoration: none;
  background-color: #fff;
  padding: 1rem;
  border-radius: 5px;
  font-size: 1.1rem;
  font-weight: bold;
  letter-spacing: 2px;
  color: #222;
}
.hero-text .hide{
   display: none;
}
@media screen and (max-width:555px) {
  .hero-text h1{
    padding:10px 20px;
    font-size: 1rem;
  }
  .hero-text p{
    font-size: 1.1rem;
    padding:0 0 2rem 0;
  }
  .hero-text .show{
    padding: 0.6rem 1.1rem;
    border-radius: 6px;
    font-size: 1rem;
  }
  
}